.CloseButton {
    background: rgba(40, 44, 52, .2);
    color: rgb(40, 44, 52);
    text-shadow: 0px 0px 10px gray;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    text-align: center;
    font-size: 3em;
    cursor: pointer;
    transition: width 1s, background 1s;
    user-select: none;
    border: 1px solid rgb(40, 44, 52);
    border-bottom-left-radius: 5px;
}

.CloseButton p {
    margin-top: -15px;
}

.CloseButton:hover {
    width: 150px;
    background: rgba(40, 44, 52, 1);
    font-size:2em;
    color:lightblue;
    text-shadow: 0px 0px 0px gray;
}

.CloseButton:hover p {
    margin-top: 0px;
}
